<template>
	<div>
        <h3>お気に入り</h3>
		<div v-for="(todo,i) in todos"  v-bind:key="i">
            <img class="img" src="./peach.png" width="20" />
			{{todo}}
			<toggle-button :labels="{checked: '買う！', unchecked: 'ある'}" 
                             v-model="toggle"
                             @change="onChangeEventHandler"
                             :color="{checked: '#00fa9a', unchecked: '#FF0000', disabled: '#CCCCCC'}"
                             width="60"></toggle-button>
		</div>
		<button v-on:click="goPageFavorite">お気に入り</button>
		<button v-on:click="goPageCheckList">買い物リスト</button>
		<button v-on:click="goPageHistory">買い物履歴</button>
	</div>
</template>
<script>
	export default{
		data(){
			return{todos:[]}
		},
		created(){
			this.todos=JSON.parse(window.localStorage.getItem('todos'))||[];
		},
		methods:{
			goPageFavorite(){
				this.$router.push('/todos/favorite');
			},
			goPageCheckList(){
				this.$router.push('/');
			},
			goPageHistory(){
				this.$router.push('/todos/history');
			}
		}
	}
</script>