<template>
	<div>
        <h3>買うもの追加</h3>
		<router-link to="/">←戻る</router-link>
		<br>
		<input v-model="newTodo" placeholder="商品名...">
		<button v-on:click="addTodo()">追加ADD</button>
	</div>
</template>
<script>
	export default{
		data(){
			return{
				newTodo:''
			}
		},
		methods:{
			addTodo(){
				if(this.newTodo==='')return;
				const todos=JSON.parse(window.localStorage.getItem('todos'))||[];
				todos.push(this.newTodo);
				window.localStorage.setItem('todos',JSON.stringify(todos));
				this.newTodo='';
				this.$router.push('/');
			}
		}
	}
</script>